import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPenaltyRegulationModel,
  IPenaltyRegulationRequest,
} from "../models/penalty-regulations";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class PenaltyRegulationsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPenaltyRegulationRequest) {
    return this.post<IResponse<IPenaltyRegulationModel>>(
      "PenaltyRegSetup/Create",
      data
    );
  }

  update(data: IPenaltyRegulationRequest) {
    return this.post<IResponse<IPenaltyRegulationModel>>(
      "PenaltyRegSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPenaltyRegulationModel[]>>(
      "PenaltyRegSetup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IPenaltyRegulationModel[]>>(
      "PenaltyRegSetup/GetAll"
    );
  }

  getById(penaltyRegulationId: number) {
    return this.get<IResponse<IPenaltyRegulationModel[]>>(
      "PenaltyRegSetup/GetAll",
      {
        params: {
          $filter: `id eq ${penaltyRegulationId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("PenaltyRegSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
