import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { violationToMap } from "app/system-setup/penalty-regulations-setup/mapper/violations";
import {
  IViolationModel,
  IViolationToDisplayModel,
} from "app/system-setup/penalty-regulations-setup/models/violations";
import { ViolationsService } from "app/system-setup/penalty-regulations-setup/services/violations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-violations-index",
  templateUrl: "./violations-index.component.html",
  styleUrls: ["./violations-index.component.scss"],
})
export class ViolationsIndexComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
  optionalColomns: string[] = [
    "variableCode",
    "display_In_Result",
    "violationType",
    "renew_Day",
    "fromValue",
    "toValue",
    "typeName",
    "moneyOrPenalty",
    "renew_By_Period",
  ];

  dataSource: MatTableDataSource<IViolationToDisplayModel>;
  isLoaded: boolean = false;

  constructor(public violationsService: ViolationsService) {
    super();
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.violationsService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IViolationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          res.data.map((violation) => {
            violation.att_Violation.moneyOrPenalty = violation.att_Violation
              .ismoney
              ? this.translate("money")
              : this.translate("penalty");
          });
          this.dataSource = new MatTableDataSource(violationToMap(res.data));
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onDelete(violation: IViolationToDisplayModel): void {
    let msg = this.msgTranslate(violation.ar_Name, violation.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(violation);
    });
  }

  delete(violation: IViolationToDisplayModel) {
    this.showLoading();
    this.violationsService.Delete(violation.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }
}
