import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IPenaltyRegulationModel } from "app/system-setup/penalty-regulations-setup/models/penalty-regulations";
import { PenaltyRegulationsService } from "app/system-setup/penalty-regulations-setup/services/penalty-regulations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-penalty-regulations-index",
  templateUrl: "./penalty-regulations-index.component.html",
  styleUrls: ["./penalty-regulations-index.component.scss"],
})
export class PenaltyRegulationsIndexComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = ["Ser", "id", "name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IPenaltyRegulationModel>;

  constructor(public penaltyRegulationsService: PenaltyRegulationsService) {
    super();
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.penaltyRegulationsService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IPenaltyRegulationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onDelete(penaltyRegulation: IPenaltyRegulationModel): void {
    let msg = this.msgTranslate(penaltyRegulation.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(penaltyRegulation);
    });
  }

  delete(penaltyRegulation: IPenaltyRegulationModel) {
    this.showLoading();
    this.penaltyRegulationsService.Delete(penaltyRegulation.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }
}
