import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { penaltyRegulationDetailsToMap } from "app/system-setup/penalty-regulations-setup/mapper/penalty-regulation";
import { violationToMap } from "app/system-setup/penalty-regulations-setup/mapper/violations";
import { IPenaltyModel } from "app/system-setup/penalty-regulations-setup/models/penalties";
import {
	IPenaltyRegulationDetailsModel,
	IPenaltyRegulationDetailsRequest,
	IEditPenaltyRegulationDetailsRequest,
	IPenaltyRegulationDetailsDeleteParams,
} from "app/system-setup/penalty-regulations-setup/models/penalty-regulations";
import { IViolationToDisplayModel, IViolationModel } from "app/system-setup/penalty-regulations-setup/models/violations";
import { PenaltiesService } from "app/system-setup/penalty-regulations-setup/services/penalties.service";
import { PenaltyRegulationsDetailsService } from "app/system-setup/penalty-regulations-setup/services/penalty-regulations-details.service";
import { ViolationsService } from "app/system-setup/penalty-regulations-setup/services/violations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-penalty-regulations-details",
	templateUrl: "./penalty-regulations-details.component.html",
	styleUrls: ["./penalty-regulations-details.component.scss"],
})
export class PenaltyRegulationsDetailsComponent extends AppHelpers implements OnInit {
	@Input() penaltyRegulationId: number;
	selectedPenaltyRegulationDetails: IPenaltyRegulationDetailsModel = null;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = ["Ser", "violation", "repetition_No", "penalty", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IPenaltyRegulationDetailsModel>;

	violationsList: IViolationToDisplayModel[] = [];
	penaltiesList: IPenaltyModel[] = [];

	constructor(
		private penaltyRegulationsDetailsService: PenaltyRegulationsDetailsService,
		private violationsService: ViolationsService,
		private penaltiesService: PenaltiesService
	) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) {
		//   this.getAllViolations();
		//   this.getAllPenalties();
		//   if (this.penaltyRegulationId > 0) this.getAll();
		// }

		this.form = this.fb.group({
			violation_ID: [null, [Validators.required]],
			repetition_No: [
				null,
				[Validators.required, CustomValidators.digits, CustomValidators.gte(0)],
			],
			penalty_ID: [null, [Validators.required]],
		});
	}

	ngOnChanges(changes: SimpleChanges): void {
		// if (changes.penaltyRegulationId.currentValue > 0) this.getAll();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllViolations();
			this.getAllPenalties();
			if (this.penaltyRegulationId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.penaltyRegulationsDetailsService
			.getAllByPenaltyRegulationId(this.penaltyRegulationId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPenaltyRegulationDetailsModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(penaltyRegulationDetailsToMap(res.data));
					this.isLoaded = true;
				} else this.errorList(res?.message?.messages);
			});
	}

	penaltyRegulationToEdit(penaltyRegulationDetails: IPenaltyRegulationDetailsModel) {
		this.form.patchValue(penaltyRegulationDetails);
		this.onChangeViolation([penaltyRegulationDetails.att_Violation]);
		this.selectedPenaltyRegulationDetails = penaltyRegulationDetails;
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IPenaltyRegulationDetailsRequest[] = [
				{
					Penalty_Regulation_ID: this.penaltyRegulationId,
					Violation_ID: this.form.value.violation_ID,
					Repetition_No: this.form.value.repetition_No ?? 0,
					Penalty_ID: this.form.value.penalty_ID,
				},
			];

			this.selectedPenaltyRegulationDetails ? this.update(model) : this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IPenaltyRegulationDetailsRequest[]) {
		this.showLoading();
		this.penaltyRegulationsDetailsService
			.create(model)
			.subscribe((res: IResponse<IPenaltyRegulationDetailsModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
				} else this.errorList(res?.message?.messages);
				this.hideLoading();
			});
	}

	update(model: IPenaltyRegulationDetailsRequest[]) {
		let editModel: IEditPenaltyRegulationDetailsRequest = {
			NewPenalityDetails: model[0],
			OldPenalityDetails: {
				Penalty_Regulation_ID: this.selectedPenaltyRegulationDetails.penalty_Regulation_ID,
				Violation_ID: this.selectedPenaltyRegulationDetails.violation_ID,
				Repetition_No: this.selectedPenaltyRegulationDetails.repetition_No ?? 0,
				Penalty_ID: this.selectedPenaltyRegulationDetails.penalty_ID,
			},
		};
		this.showLoading();
		this.penaltyRegulationsDetailsService
			.update(editModel)
			.subscribe((res: IResponse<IPenaltyRegulationDetailsModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
				} else this.errorList(res?.message?.messages);
				this.hideLoading();
			});
	}

	onDelete(penaltyRegulationDetails: IPenaltyRegulationDetailsModel): void {
		let msg = this.msgTranslate(
			`${penaltyRegulationDetails.att_Violation.ar_Name} - ${penaltyRegulationDetails.repetition_No}`,
			`${penaltyRegulationDetails.att_Violation.en_Name} - ${penaltyRegulationDetails.repetition_No}`
		);
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(penaltyRegulationDetails);
		});
	}

	delete(penaltyRegulationDetails: IPenaltyRegulationDetailsModel) {
		let params: IPenaltyRegulationDetailsDeleteParams = {
			Penalty_Regulation_ID: penaltyRegulationDetails.penalty_Regulation_ID,
			Repetition_No: penaltyRegulationDetails.repetition_No,
			Violation_ID: penaltyRegulationDetails.violation_ID,
		};

		this.showLoading();
		this.penaltyRegulationsDetailsService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (
					penaltyRegulationDetails.penalty_Regulation_ID ==
						this.selectedPenaltyRegulationDetails?.penalty_Regulation_ID &&
					penaltyRegulationDetails.violation_ID ==
						this.selectedPenaltyRegulationDetails?.violation_ID &&
					penaltyRegulationDetails.repetition_No ==
						this.selectedPenaltyRegulationDetails?.repetition_No
				)
					this.clearForm();
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset();
		this.submitted = false;
		this.selectedViolation = null;
		this.selectedPenaltyRegulationDetails = null;
	}

	getAllViolations() {
		this.showLoading();
    this.violationsService
      .getAll()
      .subscribe((res: IResponse<IViolationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.violationsList = violationToMap(res.data);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
	}

	getAllPenalties() {
		this.showLoading();
		this.penaltiesService.getAll().subscribe((res: IResponse<IPenaltyModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.penaltiesList = res.data;
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	selectedViolation: IViolationToDisplayModel = null;

	onChangeViolation(violation: IViolationToDisplayModel[]) {
		this.selectedViolation = violation[0];

		if (this.selectedViolation?.ismoney) {
			this.formCtrls.repetition_No.clearValidators();
			this.formCtrls.penalty_ID.clearValidators();
		} else {
			this.formCtrls.repetition_No.setValidators([Validators.required, CustomValidators.gte(0)]);
			this.formCtrls.penalty_ID.setValidators([Validators.required]);
		}
		this.formCtrls.repetition_No.updateValueAndValidity();
		this.formCtrls.penalty_ID.updateValueAndValidity();
	}
}
