import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IEditPenaltyRegulationDetailsRequest,
  IPenaltyRegulationDetailsDeleteParams,
  IPenaltyRegulationDetailsModel,
  IPenaltyRegulationDetailsRequest,
} from "../models/penalty-regulations";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class PenaltyRegulationsDetailsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPenaltyRegulationDetailsRequest[]) {
    return this.post<IResponse<IPenaltyRegulationDetailsModel>>(
      "PenaltyRegSetup/Create_Penalty_Regulation_Details",
      data
    );
  }

  update(data: IEditPenaltyRegulationDetailsRequest) {
    return this.post<IResponse<IPenaltyRegulationDetailsModel>>(
      "PenaltyRegSetup/Edit_Penalty_Regulation_Details",
      data
    );
  }

  getAllByPenaltyRegulationId(penaltyRegulationId: number) {
    return this.get<IResponse<IPenaltyRegulationDetailsModel[]>>(
      "PenaltyRegSetup/GetAll_Penalty_Regulation_Details",
      {
        params: {
          Penalty_Regulation_ID: penaltyRegulationId,
        },
      }
    );
  }

  Delete(params: IPenaltyRegulationDetailsDeleteParams) {
    return this.delete<IResponse>(
      "PenaltyRegSetup/Delete_Penalty_Regulation_Details",
      {
        params: params,
      }
    );
  }
}
