import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IPenaltyRegulationDetailsModel } from "../models/penalty-regulations";

export function penaltyRegulationDetailsToMap(
  penaltyRegulationDetails: IPenaltyRegulationDetailsModel[]
): IPenaltyRegulationDetailsModel[] {
  return penaltyRegulationDetails.map((penaltyRegulationDetail) => {
    return <IPenaltyRegulationDetailsModel>{
      ...penaltyRegulationDetail,
      violation: msgTranslate(
        penaltyRegulationDetail.att_Violation?.ar_Name,
        penaltyRegulationDetail.att_Violation?.en_Name
      ),
      penalty: penaltyRegulationDetail.att_Penalty?.name,
    };
  });
}
