import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ViolationTypesService } from "app/system-setup/penalty-regulations-setup/services/violation-types.service";
import {
  IViolationTypeModel,
  IViolationTypeRequest,
} from "app/system-setup/penalty-regulations-setup/models/violation-types";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-violation-types",
  templateUrl: "./violation-types.component.html",
  styleUrls: ["./violation-types.component.scss"],
})
export class ViolationTypesComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = ["Ser", "id", "name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IViolationTypeModel>;

  constructor(public violationTypesService: ViolationTypesService) {
    super();
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.violationTypesService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IViolationTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IViolationTypeRequest = {
        ID: this.form.value.id,
        Name: this.form.value.name,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IViolationTypeRequest) {
    this.showLoading();
    this.violationTypesService.create(model).subscribe((res: IResponse<IViolationTypeModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getList(this.lastPage, this.page.pageSize);
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: IViolationTypeRequest) {
    this.showLoading();
    this.violationTypesService.update(model).subscribe((res: IResponse<IViolationTypeModel>) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getList(this.currentPage, this.page.pageSize); // current page after update
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  violationTypeToEdit(violationType: IViolationTypeModel) {
    this.form.patchValue(violationType);
    // console.log(this.form.value);
  }

  onDelete(violationType: IViolationTypeModel): void {
    let msg = this.msgTranslate(violationType.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(violationType);
    });
  }

  delete(violationType: IViolationTypeModel) {
    this.showLoading();
    this.violationTypesService.Delete(violationType.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (violationType.id == this.formCtrls.id.value) this.clearForm();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
}
