import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IPenaltyRegulationModel,
  IPenaltyRegulationRequest,
} from "app/system-setup/penalty-regulations-setup/models/penalty-regulations";
import { PenaltyRegulationsService } from "app/system-setup/penalty-regulations-setup/services/penalty-regulations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-add-penalty-regulations",
  templateUrl: "./add-penalty-regulations.component.html",
  styleUrls: ["./add-penalty-regulations.component.scss"],
})
export class AddPenaltyRegulationsComponent
  extends AppHelpers
  implements OnInit
{
  form: FormGroup;
  submitted: boolean = false;

  penaltyRegulationId: number;
  // penaltyRegulation: IPenaltyRegulationModel;

  constructor(private penaltyRegulationsService: PenaltyRegulationsService) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.penaltyRegulationId = Number(params.get("penaltyRegulationId")) ?? 0;
      // if (this.penaltyRegulationId > 0 && this.CheckNoBusinessOrCompany())
      //   this.getById();
    });

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.penaltyRegulation = null;
    if (this.penaltyRegulationId > 0 && this.checkNoBusinessOrCompany())
      this.getById();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.penaltyRegulationsService
      .getById(this.penaltyRegulationId)
      .subscribe((res: IResponse<IPenaltyRegulationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.penaltyRegulation = res.data[0];
          this.penaltyRegulationToEdit(res.data[0]);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  penaltyRegulationToEdit(penaltyRegulation: IPenaltyRegulationModel) {
    this.form.patchValue(penaltyRegulation);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IPenaltyRegulationRequest = {
        ID: this.form.value.id,
        Name: this.form.value.name,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPenaltyRegulationRequest) {
    this.showLoading();
    this.penaltyRegulationsService
      .create(model)
      .subscribe((res: IResponse<IPenaltyRegulationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.penaltyRegulationId = res.data.id;
          this.formCtrls.id.setValue(this.penaltyRegulationId);
          let url = `/penalty-regulations-setup/${this.moduleId}/penalty-regulations/edit/${this.penaltyRegulationId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.penaltyRegulation = res.data;
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IPenaltyRegulationRequest) {
    this.showLoading();
    this.penaltyRegulationsService
      .update(model)
      .subscribe((res: IResponse<IPenaltyRegulationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          // this.penaltyRegulation = res.data;
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    // if (this.penaltyRegulationId == 0)
    this.form.reset({ id: 0 });
    // else this.penaltyRegulationToEdit();
    this.submitted = false;
  }
}
