import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { PenaltiesComponent } from "./components/penalties/penalties.component";
import { ViolationTypesComponent } from "./components/violation-types/violation-types.component";
import {
  PenaltyRegulationsIndexComponent
} from "./components/penalty-regulations/penalty-regulations-index/penalty-regulations-index.component";
import { AddPenaltyRegulationsComponent } from "./components/penalty-regulations/add-penalty-regulations/add-penalty-regulations.component";
import { ViolationsIndexComponent } from "./components/violations/violations-index/violations-index.component";
import { AddViolationComponent } from "./components/violations/add-violation/add-violation.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";

const routes: Routes = [
  {
    path: "penalties",
    component: PenaltiesComponent,
    data: {view: ViewsManager.penalties},
    canActivate: [CheckIsViewAllowedGuard],
  },
  {
    path: "violation-types",
    component: ViolationTypesComponent,
    data: {view: ViewsManager.violation_types},
    canActivate: [CheckIsViewAllowedGuard],
  },
  {
    path: "penalty-regulations",
    data: {view: ViewsManager.penalty_regulations},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: PenaltyRegulationsIndexComponent,
        data: {view: ViewsManager.penalty_regulations_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddPenaltyRegulationsComponent,
        data: {view: ViewsManager.add_penalty_regulations},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:penaltyRegulationId",
        component: AddPenaltyRegulationsComponent,
        data: {view: ViewsManager.add_penalty_regulations},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "violations",
    data: {view: "violations"},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: ViolationsIndexComponent,
        data: {view: ViewsManager.violations_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddViolationComponent,
        data: {view: ViewsManager.add_violation},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:violationId",
        component: AddViolationComponent,
        data: {view: ViewsManager.add_violation},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PenaltyRegulationsSetupRoutingModule {}
