import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPenaltyModel, IPenaltyRequest } from "../models/penalties";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class PenaltiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPenaltyRequest) {
    return this.post<IResponse<IPenaltyModel>>("PenalitiesSetup/Create", data);
  }

  update(data: IPenaltyRequest) {
    return this.post<IResponse<IPenaltyModel>>("PenalitiesSetup/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPenaltyModel[]>>("PenalitiesSetup/GetList", {
      params: params,
    });
  }

  getAll() {
    return this.get<IResponse<IPenaltyModel[]>>("PenalitiesSetup/GetAll").pipe(
      map((res) => {
        res.data.map((item: IPenaltyModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("PenalitiesSetup/Delete", {
      params: { Id: Id },
    });
  }
  
  exportExcel() {
    this.getAll();
  }
}
