import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { violationToMap } from "app/system-setup/penalty-regulations-setup/mapper/violations";
import { IViolationTypeModel } from "app/system-setup/penalty-regulations-setup/models/violation-types";
import {
  IViolationAllType,
  IViolationToDisplayModel,
  IViolationRequest,
  IViolationModel,
} from "app/system-setup/penalty-regulations-setup/models/violations";
import { ViolationTypesService } from "app/system-setup/penalty-regulations-setup/services/violation-types.service";
import { ViolationsService } from "app/system-setup/penalty-regulations-setup/services/violations.service";
import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-add-violation",
  templateUrl: "./add-violation.component.html",
  styleUrls: ["./add-violation.component.scss"],
})
export class AddViolationComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  violationId: number;
  // violation: IViolationToDisplayModel;

  violationTypesList: IViolationTypeModel[] = [];
  typesList: Idropdown[] = [];

  constructor(
    private violationsService: ViolationsService,
    private violationTypesService: ViolationTypesService
  ) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.violationId = Number(params.get("violationId")) ?? 0;
      // if (this.CheckNoBusinessOrCompany()) {
      //   this.getAllViolationTypes();
      //   this.getAllTypes();
      //   if (this.violationId > 0) this.getById();
      // }
    });

    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
        ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
        variableCode: [null, CustomValidator.variableCodeValidators],
        display_In_Result: [false],
        renew_Day: [null, [CustomValidators.gte(0), CustomValidators.digits]],
        fromValue: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.digits,
          ],
        ],
        toValue: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.digits,
          ],
        ],
        violation_Type_ID: [null, [Validators.required]],
        type: [null, [Validators.required]],
        renew_By_Period: [false],
        ismoney: [false],
      },
      {
        validator: CustomValidator.greaterThan("fromValue", "toValue"),
      }
    );
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.violation = null;
    if (this.checkNoBusinessOrCompany()) {
      this.getAllViolationTypes();
      this.getAllTypes();
      if (this.violationId > 0) this.getById();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.violationsService
      .getById(this.violationId)
      .subscribe((res: IResponse<IViolationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.violation = res.data[0];
          let result: IViolationToDisplayModel[] = violationToMap(res.data);
          this.violationToEdit(result[0]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  violationToEdit(violation: IViolationToDisplayModel) {
    this.form.patchValue(violation);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IViolationRequest = {
        Violation: {
          ID: this.form.value.id,
          En_Name: this.form.value.en_Name,
          Ar_Name: this.form.value.ar_Name,
          Renew_Day: this.form.value.renew_Day,
          FromValue: this.form.value.fromValue,
          ToValue: this.form.value.toValue,
          Violation_Type_ID: this.form.value.violation_Type_ID,
          Type: this.form.value.type,
          Renew_By_Period: this.form.value.renew_By_Period ?? false,
          Ismoney: this.form.value.ismoney,
        },
        VariableCode: this.form.value.variableCode,
        Display_In_Result: this.form.value.display_In_Result ?? false,
      };

      model.Violation.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IViolationRequest) {
    this.showLoading();
    this.violationsService
      .create(model)
      .subscribe((res: IResponse<IViolationToDisplayModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.violationId = res.data.id;
          this.formCtrls.id.setValue(this.violationId);
          let url = `/penalty-regulations-setup/${this.moduleId}/violations/edit/${this.violationId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.violation = res.data;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IViolationRequest) {
    this.showLoading();
    this.violationsService
      .update(model)
      .subscribe((res: IResponse<IViolationToDisplayModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          // this.violation = res.data;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    // if (this.violationId == 0)
    this.form.reset({
      id: 0,
      ismoney: false,
    });
    // else this.violationToEdit();
    this.submitted = false;
  }

  getAllViolationTypes() {
    this.showLoading();
    this.violationTypesService
      .getAll()
      .subscribe((res: IResponse<IViolationTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.violationTypesList = res.data;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  getAllTypes() {
    this.showLoading();
    this.violationsService
      .getAllTypes()
      .subscribe((res: IResponse<IViolationAllType[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.typesList = res.data;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  moneyOrPenaltyRadioList: RadioButtonModel[] = [
    {
      value: true,
      label: "money",
    },
    {
      value: false,
      label: "penalty",
    },
  ];
}
